"use client";

import React, { useEffect, useRef } from 'react';
import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import Draggable from 'gsap/Draggable';
import './CoverFlow.css'; // Compile your Stylus into CSS

gsap.registerPlugin(ScrollTrigger, Draggable);

const COVERS = [
  "./img1.jpg",
  "./img2.jpg",
  "./img3.jpg",
  "./img4.jpg",
  "./img5.jpg",
  "./img6.jpg",
  "./img7.jpg",
  "./img8.jpg",
  "./img9.jpg",
  "./img10.jpg",
  "./img11.jpg",
  "./img12.jpg",
  "./img13.jpg",
  "./img14.jpg",
  "./img15.jpg",
  "./img16.jpg",
  "./img17.jpg",
  "./img18.jpg",
  "./img19.jpg",
  "./img21.jpg",
  "./img22.jpg",
  "./img23.jpg",
  "./img24.jpg",
  "./img25.jpg",
  "./img26.jpg",
  "./img27.jpg",
  "./img28.jpg",
  "./img29.jpg",
  "./img31.jpg",
  "./img32.jpg",
  "./img33.jpg",
  "./img34.jpg",
  "./img35.jpg",
  "./img36.jpg",
  "./img37.jpg",
  "./img38.jpg",
  "./img39.jpg",
  "./img41.jpg",
  "./img42.jpg",
  "./img43.jpg",
];

const Carousel: React.FC = () => {
  const boxesRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!boxesRef.current) return;
  
    const BOXES = gsap.utils.toArray('.box') as HTMLElement[];
  
    gsap.set(BOXES, { yPercent: -50, display: 'block' });
    gsap.set('button', { z: 200 });
  
    const STAGGER = 0.1;
    const DURATION = 1;
    const OFFSET = 0;
    const LOOP = gsap.timeline({ paused: true, repeat: -1, ease: 'none' });
    const SHIFTS = [...BOXES, ...BOXES, ...BOXES];
  
    // Setup animation timeline
    SHIFTS.forEach((BOX, index) => {
      const BOX_TL = gsap
        .timeline()
        .set(BOX, { xPercent: 250, rotateY: -50, opacity: 0, scale: 0.5 })
        .to(BOX, { opacity: 1, scale: 1, duration: 0.1 }, 0)
        .to(BOX, { opacity: 0, scale: 0.5, duration: 0.1 }, 0.9)
        .fromTo(
          BOX,
          { xPercent: 250 },
          { xPercent: -350, duration: 1, immediateRender: false, ease: 'power1.inOut' },
          0
        )
        .fromTo(
          BOX,
          { rotateY: -50 },
          { rotateY: 50, immediateRender: false, duration: 1, ease: 'power4.inOut' },
          0
        )
        .to(BOX, { z: 100, scale: 1.25, duration: 0.1, repeat: 1, yoyo: true }, 0.4)
        .fromTo(
          BOX,
          { zIndex: 1 },
          { zIndex: BOXES.length, repeat: 1, yoyo: true, ease: 'none', duration: 0.5, immediateRender: false },
          0
        );
  
      LOOP.add(BOX_TL, index * STAGGER);
    });
  
    const CYCLE_DURATION = STAGGER * BOXES.length;
    const START_TIME = CYCLE_DURATION + DURATION * 0.5 + OFFSET;
  
    const LOOP_HEAD = gsap.fromTo(
      LOOP,
      { totalTime: START_TIME },
      { totalTime: `+=${CYCLE_DURATION}`, duration: 1, ease: 'none', repeat: -1, paused: true }
    );
  
    const PLAYHEAD = { position: 0 };
    const POSITION_WRAP = gsap.utils.wrap(0, LOOP_HEAD.duration());
  
    const SCRUB = gsap.to(PLAYHEAD, {
      position: 0,
      onUpdate: () => LOOP_HEAD.totalTime(POSITION_WRAP(PLAYHEAD.position)),
      paused: true,
      duration: 0.25,
      ease: 'power3',
    });
  
    // Wheel scrolling instead of ScrollTrigger
    const scrollSpeed = 0.09
    const onWheel = (e: WheelEvent) => {
      e.preventDefault(); // prevent page scroll
      const delta = e.deltaY;
      const direction = delta > 0 ? 1 : -1;
      SCRUB.vars.position += direction * (1 / BOXES.length) * scrollSpeed;
      SCRUB.invalidate().restart();
    };
  
    const container = boxesRef.current;
    container.addEventListener('wheel', onWheel, { passive: false });
  
    // Keyboard navigation
    const NEXT = () => {
      SCRUB.vars.position -= 1 / BOXES.length;
      SCRUB.invalidate().restart();
    };
    const PREV = () => {
      SCRUB.vars.position += 1 / BOXES.length;
      SCRUB.invalidate().restart();
    };
  
    const handleKey = (event: KeyboardEvent) => {
      if (event.code === 'ArrowLeft' || event.code === 'KeyA') NEXT();
      if (event.code === 'ArrowRight' || event.code === 'KeyD') PREV();
    };
    document.addEventListener('keydown', handleKey);
  
    // Buttons
    const nextBtn = container.querySelector('.next');
    const prevBtn = container.querySelector('.prev');
    nextBtn?.addEventListener('click', NEXT);
    prevBtn?.addEventListener('click', PREV);
  
    // Dragging
    Draggable.create('.drag-proxy', {
      type: 'x',
      trigger: '.box',
      onPress() {
        this.startOffset = SCRUB.vars.position;
      },
      onDrag() {
        SCRUB.vars.position = this.startOffset + (this.startX - this.x) * 0.001;
        SCRUB.invalidate().restart();
      },
      onDragEnd() {
        // nothing else needed
      },
    });
  
    return () => {
      container.removeEventListener('wheel', onWheel);
      document.removeEventListener('keydown', handleKey);
      gsap.killTweensOf(PLAYHEAD);
    };
  }, []);
  

  return (
    <>
    <div className="carousel-wrapper" ref={boxesRef}>
  <div className="boxes">
    {COVERS.map((cover, index) => (
      <div className="box" key={index} style={{ '--src': `url(${cover})` } as React.CSSProperties}>
        <span>{index + 1}</span>
        <img src={cover} alt={`Album ${index + 1}`} />
      </div>
    ))}
  </div>


      <svg className="scroll-icon" viewBox="0 0 24 24">
        <path
          fill="currentColor"
          d="M20 6H23L19 2L15 6H18V18H15L19 22L23 18H20V6M9 3.09C11.83 3.57 14 6.04 14 9H9V3.09M14 11V15C14 18.3 11.3 21 8 21S2 18.3 2 15V11H14M7 9H2C2 6.04 4.17 3.57 7 3.09V9Z"
        />
      </svg>
  <div className="drag-proxy"></div>
</div>

    </>
  );
};

export default Carousel;
